:host {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    border-radius: 0.25rem;

    background: url("data:image/svg+xml;charset=utf-8, <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8' style='fill-rule:evenodd;'><path fill='rgba(186,186,192,0.16)' d='M0 0h4v4H0zM4 4h4v4H4z'/></svg>");
    background-size: 0.5rem;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
    overflow: hidden;

    &.is-resizable {
        resize: horizontal;
        max-width: 100%;
        min-width: 10rem;

        &::after {
            content: 'Resize me â¤µ';
            font-size: 0.75rem;
            position: absolute;
            right: 0.25rem;
